  methods: {
    submit(e) {
      var that = this
      that.input_item = ''
      that.result = ''
      var data = {
        ipim: that.ipim,
        pH: that.pH,
        cItem: that.cItem
      }
      console.log('submit', data)
      that.$http.post(that.$api + 'phcal', data).then(
        (res) => {
          console.log('res', res.data)
          if (res.data.error === 0) {
            that.result = res.data.result
            that.rions = res.data.ions
            console.log(that.rions, 'rions')
          } else {
            that.cH = res.data.result
            that.input_item = {
              ipim: res.data.ipim,
              pH: res.data.pH
            }
          }
        }
      )
    },
    addExistingIons(values) {
      console.log('adding existing ions', values, this.ExpStepper)
      if (values) {
        var number = values.ionNum
        values.ionNum = (parseFloat(number) * (10 ** parseFloat(this.ExpStepper)))
        console.log(values)
        var idx = this.cItem.findIndex(obj => obj.ionNam === values.ionNam)
        if (idx === -1) {
          this.cItem.push(values)
        }
        console.log(this.cItem)
      }
    },
    removeIon(iName) {
      var idx = this.cItem.findIndex(obj => obj.ionNam === iName)
      this.cItem.splice(idx, 1)
    },
    rion2ci(rion) {
      this.cItem.splice(0)
      var cikeys = Object.keys(rion)
      var civals = Object.values(rion)
      for (var i = 0; i < cikeys.length; i++) {
        this.cItem.push({
          ionNam: cikeys[i],
          ionNum: (civals[i])
        })
      }
    },
    onFinish({
      selectedOptions
    }) {
      this.cascaderShow = false;
      this.fieldValue = selectedOptions[1].value;
    },
    onFinishIpim({
      selectedOptions
    }) {
      this.ipimShow = false;
      console.log(selectedOptions)
      this.ipim = selectedOptions[1].value;
    },
    visDisplay(inputItem) {
      for (var i in this.options) {
        var obj = this.options[i].children.findIndex(obj => obj.value === inputItem)
        if (obj !== -1) {
          return this.options[i].children[obj].text
        }
      }
    }
