import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import axios from 'axios'
import VueAxios from 'vue-axios'
import qs from 'qs';

import { Button, Form, Stepper, Col, Row, Divider, NoticeBar, Cascader, Popup } from 'vant';

Vue.prototype.$http = axios
Vue.prototype.$qs = qs

Vue.prototype.$api = 'http://127.0.0.1:8000/'
// Vue.prototype.$api = 'https://scbackend.jiangyq.com/sc/'
// Vue.prototype.$api = 'https://wx.aijuncai.com/sc/'

Vue.config.productionTip = false
Vue.use(VueAxios, axios)
Vue.use(Button)
Vue.use(Form)
Vue.use(Stepper)
Vue.use(Col);
Vue.use(Row);
Vue.use(Divider);
Vue.use(NoticeBar);
Vue.use(Cascader);
Vue.use(Popup);

new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')
